@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  min-height: 300px;
  border: 1px solid #111;
  border-radius: 2px;
  overflow: auto;
  margin: 30px auto;
  padding: 30px;
}

.user-data {
  display: flex;
  justify-content: space-between;
  margin-top: -20px;
  margin-bottom: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #111;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
  margin: 15px 0;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.task section {
  display: none;
}

.task.details {
  border-left: 5px solid #007af3;
}

.task.details section {
  display: flex;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 5px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 0px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control input[type='text'] {
  font-family: inherit;
}

.form-control input[type='number'] {
  font-family: inherit;
}

.filter-icon {
  font-size: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 12px;
  cursor: pointer;
}

.filter-icon:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

footer {
  margin-top: 30px;
  text-align: center;
}

